import Vue from 'vue';
import i18n from '../../../i18n';
import { emojilist } from '../../../../../misc/emojilist';
import { getStaticImageUrl } from '../../../common/scripts/get-static-image-url';
import { faAsterisk, faLeaf, faUtensils, faFutbol, faCity, faDice, faGlobe, faHistory, faUser } from '@fortawesome/free-solid-svg-icons';
import { faHeart, faFlag, faLaugh } from '@fortawesome/free-regular-svg-icons';
import { groupByX } from '../../../../../prelude/array';
export default Vue.extend({
    i18n: i18n('common/views/components/emoji-picker.vue'),
    data() {
        return {
            emojilist,
            getStaticImageUrl,
            customEmojis: {},
            faGlobe, faHistory,
            q: null,
            searchResults: [],
            categories: [{
                    text: this.$t('custom-emoji'),
                    icon: faAsterisk,
                    isActive: true
                }, {
                    name: 'face',
                    text: this.$t('face'),
                    icon: faLaugh,
                    isActive: false
                }, {
                    name: 'people',
                    text: this.$t('people'),
                    icon: faUser,
                    isActive: false
                }, {
                    name: 'animals_and_nature',
                    text: this.$t('animals-and-nature'),
                    icon: faLeaf,
                    isActive: false
                }, {
                    name: 'food_and_drink',
                    text: this.$t('food-and-drink'),
                    icon: faUtensils,
                    isActive: false
                }, {
                    name: 'activity',
                    text: this.$t('activity'),
                    icon: faFutbol,
                    isActive: false
                }, {
                    name: 'travel_and_places',
                    text: this.$t('travel-and-places'),
                    icon: faCity,
                    isActive: false
                }, {
                    name: 'objects',
                    text: this.$t('objects'),
                    icon: faDice,
                    isActive: false
                }, {
                    name: 'symbols',
                    text: this.$t('symbols'),
                    icon: faHeart,
                    isActive: false
                }, {
                    name: 'flags',
                    text: this.$t('flags'),
                    icon: faFlag,
                    isActive: false
                }]
        };
    },
    watch: {
        q() {
            if (this.q == null || this.q === '') {
                this.searchResults = [];
                return;
            }
            const q = this.q.replace(/:/g, '');
            const searchCustom = () => {
                const max = 8;
                const emojis = (this.$root.getMetaSync() || { emojis: [] }).emojis || [];
                const matches = new Set();
                const exactMatch = emojis.find(e => e.name === q);
                if (exactMatch)
                    matches.add(exactMatch);
                if (q.includes(' ')) { // AND検索
                    const keywords = q.split(' ');
                    // 名前にキーワードが含まれている
                    for (const emoji of emojis) {
                        if (keywords.every(keyword => emoji.name.includes(keyword))) {
                            matches.add(emoji);
                            if (matches.size >= max)
                                break;
                        }
                    }
                    if (matches.size >= max)
                        return matches;
                    // 名前またはエイリアスにキーワードが含まれている
                    for (const emoji of emojis) {
                        if (keywords.every(keyword => emoji.name.includes(keyword) || emoji.aliases.some(alias => alias.includes(keyword)))) {
                            matches.add(emoji);
                            if (matches.size >= max)
                                break;
                        }
                    }
                }
                else {
                    for (const emoji of emojis) {
                        if (emoji.name.startsWith(q)) {
                            matches.add(emoji);
                            if (matches.size >= max)
                                break;
                        }
                    }
                    if (matches.size >= max)
                        return matches;
                    for (const emoji of emojis) {
                        if (emoji.aliases.some(alias => alias.startsWith(q))) {
                            matches.add(emoji);
                            if (matches.size >= max)
                                break;
                        }
                    }
                    if (matches.size >= max)
                        return matches;
                    for (const emoji of emojis) {
                        if (emoji.name.includes(q)) {
                            matches.add(emoji);
                            if (matches.size >= max)
                                break;
                        }
                    }
                    if (matches.size >= max)
                        return matches;
                    for (const emoji of emojis) {
                        if (emoji.aliases.some(alias => alias.includes(q))) {
                            matches.add(emoji);
                            if (matches.size >= max)
                                break;
                        }
                    }
                }
                return matches;
            };
            const searchUnicode = () => {
                const max = 8;
                const emojis = this.emojilist;
                const matches = new Set();
                const exactMatch = emojis.find(e => e.name === q);
                if (exactMatch)
                    matches.add(exactMatch);
                if (q.includes(' ')) { // AND検索
                    const keywords = q.split(' ');
                    // 名前にキーワードが含まれている
                    for (const emoji of emojis) {
                        if (keywords.every(keyword => emoji.name.includes(keyword))) {
                            matches.add(emoji);
                            if (matches.size >= max)
                                break;
                        }
                    }
                    if (matches.size >= max)
                        return matches;
                    // 名前またはエイリアスにキーワードが含まれている
                    for (const emoji of emojis) {
                        if (keywords.every(keyword => emoji.name.includes(keyword) || emoji.keywords.some(alias => alias.includes(keyword)))) {
                            matches.add(emoji);
                            if (matches.size >= max)
                                break;
                        }
                    }
                }
                else {
                    for (const emoji of emojis) {
                        if (emoji.name.startsWith(q)) {
                            matches.add(emoji);
                            if (matches.size >= max)
                                break;
                        }
                    }
                    if (matches.size >= max)
                        return matches;
                    for (const emoji of emojis) {
                        if (emoji.keywords.some(keyword => keyword.startsWith(q))) {
                            matches.add(emoji);
                            if (matches.size >= max)
                                break;
                        }
                    }
                    if (matches.size >= max)
                        return matches;
                    for (const emoji of emojis) {
                        if (emoji.name.includes(q)) {
                            matches.add(emoji);
                            if (matches.size >= max)
                                break;
                        }
                    }
                    if (matches.size >= max)
                        return matches;
                    for (const emoji of emojis) {
                        if (emoji.keywords.some(keyword => keyword.includes(q))) {
                            matches.add(emoji);
                            if (matches.size >= max)
                                break;
                        }
                    }
                }
                return matches;
            };
            const searchResultCustom = Array.from(searchCustom());
            const searchResultUnicode = Array.from(searchUnicode());
            this.searchResults = searchResultCustom.concat(searchResultUnicode);
        }
    },
    created() {
        let local = (this.$root.getMetaSync() || { emojis: [] }).emojis || [];
        local = groupByX(local, (x) => x.category || '');
        this.customEmojis = local;
        if (this.$store.state.device.activeEmojiCategoryName) {
            this.goCategory(this.$store.state.device.activeEmojiCategoryName);
        }
    },
    methods: {
        go(category) {
            this.goCategory(category.name);
        },
        goCategory(name) {
            let matched = false;
            for (const c of this.categories) {
                c.isActive = c.name === name;
                if (c.isActive) {
                    matched = true;
                    this.$store.commit('device/set', { key: 'activeEmojiCategoryName', value: c.name });
                }
            }
            if (!matched) {
                this.categories[0].isActive = true;
            }
        },
        chosen(emoji) {
            const getKey = (emoji) => emoji.char || `:${emoji.name}:`;
            let recents = this.$store.state.device.recentEmojis || [];
            recents = recents.filter((e) => getKey(e) !== getKey(emoji));
            recents.unshift(emoji);
            this.$store.commit('device/set', { key: 'recentEmojis', value: recents.splice(0, 16) });
            this.$emit('chosen', getKey(emoji));
        }
    }
});
